@use '../styles.scss' as *;

.projects {
  display: flex;
  width: 100%;
  height: 100vh;
  color: #000;
  overflow: hidden;

  .left {
    flex: 0 0 30%;
    display: flex;
    align-items: center;
    justify-content: center;

    h2{
      font-family: 'pretendard_b';
      font-size: 5rem;
      color: #fff;

      span{
        color: $point;
      }
    }
  }

  .right {
    flex: 1;
    overflow: hidden;
    display: flex;

    .scroll {
      display: flex;
      flex-wrap: nowrap;
      overflow-x: hidden;
      scroll-behavior: smooth;
      width: 100%;

      .card {
        flex: 0 0 100%;
        height: 100vh;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: #ffffffd2;
        transition: transform 0.5s;

        h4{
          font-size: 3rem;
          text-align: center;
        }

        img{
          padding-top: 30px;
        }

        .skill{
          display: flex;
          justify-content: space-between;
          font-size: 20px;
          padding: 0 20px;

          p{
            align-self: center;
          }

          a{
            font-family: 'pretendard_b';
            display: flex;
            align-items: center;

            &:hover{
              color: $point;
            }
          }
        }

        
        .description{
          padding: 20px;
        }
      }
    }
  }
}
